* {
  box-sizing: border-box;
  list-style: none;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background: linear-gradient(90deg, #0e0ec8 26%, #050453 61%, #020024 75%, #60f 94%);
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  justify-content: flex-start;
  display: flex;
}

button {
  cursor: pointer;
  color: red;
  text-transform: uppercase;
  background: none;
  border: none;
  margin: 5px;
  padding: 10px;
  transition: transform .9s;
  animation: button 2s ease-out infinite;
}

button:hover {
  color: azure;
  background-color: #d2691e;
  border: none;
  border-radius: 5px;
  outline: none;
  margin-left: 2px;
  margin-right: 2px;
  transform: scale(1.2);
}

#submit:hover {
  animation: none;
}

